<template>
  <div class="login">
    <div class="login_bg">
      <img class="gif-bg" src="@/assets/timge.gif" alt="" />
    </div>
    <div class="login_main">
      <div class="login-content">
        <h1 class="title">登 录</h1>
        <van-form ref="form">
          <van-field
            v-model="form.mobile"
            name="mobile"
            left-icon="graphic"
            placeholder="请输入手机号"
            :rules="rules.mobile"
          />
          <van-field
            v-model="form.code"
            name="code"
            left-icon="lock"
            placeholder="请输入验证码"
            :rules="rules.code"
          >
            <template #button class="codeBtn">
              <div class="getCode" @click="getCode" v-if="totalTime === 6">
                获取验证码
              </div>
              <div class="getCode" v-else>{{ totalTime }}</div>
            </template>
          </van-field>
          <div style="margin: 16px;">
            <van-button
              class="login-btn"
              block
              type="info"
              native-type="submit"
              @click="login"
            >
              登 录
            </van-button>
            <div class="clause"><u> 隐私条款</u></div>
          </div>
        </van-form>
      </div>
    </div>
  </div>
</template>

<script>
import { apiLogin } from '@/api/use.js'
import { localSet, localGet } from '@/utils/mylocal.js'

export default {
  data () {
    return {
      totalTime: 6,
      form: {
        mobile: '',
        code: ''
      },
      rules: {
        mobile: [
          { required: 'true', message: '请输入手机号', trigger: 'onChange' },
          {
            validator: value => {
              return /^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57])[0-9]{8}$/.test(
                value
              )
            },
            message: '请正确输入手机号',
            trigger: 'onChange'
          }
        ],
        code: [
          { required: 'true', message: '请输入验证码', trigger: 'onChange' }
        ]
      }
    }
  },
  methods: {
    login () {
      this.$refs.form.validate().then(() => {
        apiLogin(this.form)
          .then(res => {
            console.log(res)
            localSet('token', res.data.data)
            this.$router.push(
              this.$route.query.next ? this.$route.query.next : '/index'
            )
            // this.$store.comments('setUserInfo',res)
          })
          .catch(() => {
            this.$toast.fail('验证失败')
          })
      })
    },
    getCode () {
      this.$refs.form.validate('mobile').then(() => {
        this.totalTime--
        const _interval = setInterval(() => {
          this.totalTime--
          if (this.totalTime <= 0) {
            clearInterval(_interval)
            this.totalTime = 6
          }
        }, 1000)
        this.$toast.success('246810')
      })
    }
  },
  created () {
    if (localGet('token')) {
      this.$router.push(
        this.$route.query.next ? this.$route.query.next : '/index'
      )
    }
  }
}
</script>

